.footer {
  background: url(../images/footer-bg.jpg) no-repeat center top;
  background-size: cover;
  padding: 68px 0 24px;
  @include ffFFCL;
  color: #303030;
  font-size: 18px;
  @media (max-width: 1024px) {
    font-size: 16px; }
  @media (max-width: 800px) {
    padding: 20px 0; }
  a {
    color: #000; }
  &__copyright {
    margin-top: 59px;
    padding-top: 18px;
    border-top: #fff solid 1px;
    letter-spacing: -1px;
    @media (max-width: 1024px) {
      margin-top: 20px; } }
  &__link {
    font-size: 20px;
    line-height: 26px;
    color: #303030;
    text-decoration: none;
    display: block;
    @include ffFFCB;
    @media (max-width: 1024px) {
      font-size: 16px;
      line-height: 20px; } }
  .btn {
    margin-top: 8px; }
  nav {
    margin-top: 15px;
    @media (max-width: 1024px) {
      margin-top: 0;
      text-align: center; }
    ul {
      display: block;
      margin: 0;
      padding: 0;
      li {
        margin: 0;
        padding: 0;
        float: left;
        display: block;
        list-style: none;
        padding-right: 28px;
        &:nth-last-child(1) {
          padding-right: 0; }
        @media (max-width: 1024px) {
          padding-right: 0;
          padding-left: 15px; }
        a {
          font-size: 18px;
          color: #2f2f2f;
          @include ffFFCL; } } } } }
